@import "../../global.scss";



.intro {
    background-color: white;
    display: flex;

    .background{ 
        opacity: 0.2;
        height: 80%;
    }

    @include mobile{
        flex-direction: column;
        align-items: center;
      }

    .left{
        flex: 0.5;
        overflow: hidden;
        
        .imgContainer{
            width: 700px;
            height: 700px;
            display: flex;
            justify-content: center;
            align-items: center;


        }
    }   

    .right{
        flex: 0.5;

        .wrapper {
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            
            @include mobile{
                padding-left: 0;
                align-items: center;
              }
            
            h1 {
                font-size: 55px;
                margin: 10px;
            }

            @include mobile{
                font-size: 40px;
              }

            h2 {
                font-size: 30px;
            }

            h3 {
                font-size: 20px;

                @include mobile{
                    font-size: 20px;
                  }

                span{
                    font-size: inherit;
                    color:darkgreen;
                }
            }
        }

        a{
            position: absolute;
            bottom: 10px;
            left: 50%;

            img{
                width: 30px;
            }
        }

    }
}