@import "../../global.scss";

.topbar {
    width : 100%;
    height : 78px;
    background-color: white;
    color: $mainColor;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 2s ease;

    .wrapper{
        padding: 10px 30px;
        display:flex;
        align-items: center;
        justify-content: space-between;
        
    
        .left {
            display: flex;
            align-items: center;

            .logo {
            font-size:  40px;
            font-weight: 1000;
            text-decoration: none;
            color: inherit;
            margin-right: 40px;
            }

            .itemContainer {
              display: flex;
              align-items: center;
              margin: 20px;
              
              @include mobile{
                display: none;
              }

              .icon {
                font-size: 18px;
                margin-right: 10px;
              }

              span {
                font-size: 15px;
                font-weight: 400;
              }
            }
        }

        .right {
            
            .hamburger {
                width: 32px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;


                span{
                    width: 100%;
                    height: 3px;
                    background-color: black;
                    transform-origin: left;
                    transition: all 2s ease;
                }
            }
        }
    }

    &.active {
        background-color: $mainColor;
        color: white;

        .hamburger {
            span{
                &:first-child{
                    background-color: white;
                    transform: rotate(45deg);
                }
                &:last-child{
                    background-color: white;
                    transform: rotate(-45deg);
                }
                &:nth-child(2){
                    opacity: 0;
                }
                
            }
        }
    }
}